@import url("https://fonts.googleapis.com/css2?family=Anton&family=Open+Sans+Condensed:wght@300;700&family=Oswald:wght@300&family=Righteous&display=swap");
.dashboard-body {
  background: whitesmoke;
  height: 100vh;
  overflow-x: hidden;
  padding-top: 150px;
}
.card-dash {
  background: white;
  height: 100%;
  box-shadow: 0px 0px 10px #44444499;
  border-radius: 2px;
}
.card-dash img {
  width: 100px;
  margin-top: 20px;
  border-radius: 100px;
  background: white;
  padding: 10px;
}
.card-dash .col-md-2 {
  text-align: center;
  padding: 0px;
}
.card-dash ul {
  list-style-type: none;
  padding: 0px;
  margin-top: 30px;
  color: white;
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: 700;
}
.card-dash ul li {
  padding-bottom: 10px;
  padding-top: 10px;
  margin-top: 20px;
  transition: all 0.2s ease-out;
}
.card-dash ul li.active,
.card-dash ul li:hover {
  background: white;
  color: #444444;
  transition: all 0.2s ease-out;
  box-shadow: -2px 2px 2px #444444a8;
}
.card-dash .col-md-9 {
  padding-top: 40px;
}
