.card-dash h2 {
  text-transform: capitalize;
  font-family: "Oswald", sans-serif;
  margin: 0px;
  font-weight: 700;
  font-size: 48px;
  padding: 0px;
}
.card-dash h4 {
  font-family: "Open Sans";
  font-size: 18px;
  margin: 0px;
  margin-top: 10px;
  padding: 0px;
}
.inner_cards_dash {
  width: 100%;
  margin-bottom: 20px;
  padding: 0px;
  transition: all 0.2s ease-out;
  box-shadow: 0px 8px 10px #44444473;
  border-radius: 5px 5px 0px 0px;
}

.inner_cards_dash:hover {
  box-shadow: 0px 0px 10px #444444;
  transition: all 0.2s ease-out;
}

.inner_cards_dash h6 {
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: 700;
  margin: 0px;
  color: white;
}
.inner_card_dash_header {
  border-bottom: 1px solid #dadada;
  margin: 0px;
  height: 20%;
  padding: 10px 0px 10px 15px;
  border-radius: 5px 5px 0px 0px;
}
.inner_card_dash_header svg {
  margin-top: -20px;
  color: white;
}
.inner_card_dash_body {
  border: 1px solid #80808042;
  border-top: 0px !important;
  background: white;
  padding: 10px;
}
.inner_cards_dash input {
  font-size: 18px;
  color: black;
  font-weight: 300 !important;
  margin: 0px;
  font-family: "Oswald", sans-serif !important;
  border: 0px;
  width: 100%;
}
.inner_cards_dash input:focus {
  outline: none;
}
.twitter .inner_card_dash_header {
  box-shadow: 0px 0px 50px #38a1f2 inset;
  padding: 10px;
}
.twitter:hover {
  box-shadow: 0px 0px 10px #38a1f2;
  transition: all 0.2s ease-out;
}

.youtube .inner_card_dash_header {
  box-shadow: 0px 0px 50px #c00 inset;
  padding: 10px;
}

.youtube:hover {
  box-shadow: 0px 0px 10px #c00;
}
